const instance = axios.create()


// 
instance.interceptors.request.use(
    (config) => {
        // 1. 
        // 2.  
        // config.headers['webClient'] = 1 // 
        // cancelToken 
        // config.cancelToken = new axios.CancelToken(e => {
        //   // 
        //   requestList.has(config.url)
        //     ? e(`${location.host}${config.url}---`)
        //     : requestList.add(config.url)
        // })

        // console.log(config.url)
        // 
        // if(!process.env.NODE_ENV=="development"){
        //     config.url= config.url.replace("/","/dev-api/")
        // }

        // console.log(111)
        store.dispatch("changeGlobalLodding",true) // lodding

        return config;
    },
    (error) => {
        store.dispatch("changeGlobalLodding",false) // lodding

        // return Promise.reject(error)
    }
)

// 
instance.interceptors.response.use(
    (response)=>{
        const {config,data,status}=response;
        store.dispatch("changeGlobalLodding",false) // lodding

        // console.log(response)  
        // 
        if(status==401){
            showFailToast("")
            // router.push('/login')
            return undefined
        }

        // 
        if (response.status == 200) {
            return data
            // if( ["post","get"].includes(String(config["method"])) ){
            //     return data
            // }else{
            //     return undefined
            // }
        }else{
            return undefined
        }
    },
    (error)=>{
        const {status}=error.response
        
        store.dispatch("changeGlobalLodding",false) // lodding


        // 
        if(status==401){
            showFailToast("")
            router.push('/login')
        }
        // console.log(error.response)
        // const {statusText,status,data={}}=error.response;
        // message.error(`[${status}] ${statusText} ${data.message}`);
        // store.dispatch("changeSpinning",false) // lodding

        store.dispatch("changeGlobalLodding",false) // lodding
    }
)



export default instance